import init, {
  WebCache,
  Signup,
  generatePassphrase,
} from "@saveoursecrets/wasm-bindings";
import * as Comlink from "comlink";

void (async function () {
  // Requires top-level await experiment
  await init();
  console.log("Worker finished initializing");
  self.postMessage({ ready: true });
})();

Comlink.expose({ WebCache, Signup, generatePassphrase });
